<template>
  <div id="myshops" :class="skins=='days'?'white':'white balck'">
    <div class="title bgf8">{{$t('fat.myshops')}}</div>
    <div class="list-box bgf8">
      <div class="list-header flex">
        <div>{{$t('fat.name')}}</div>
        <div>{{$t('fat.fiatSub')}}</div>
        <div>{{$t('fat.register_time')}}</div>
        <div>{{$t('fat.shoper_balance')}}</div>
        <!-- <div>{{$t('td.method')}}</div> -->
        <div>{{$t('td.do')}}</div>
      </div>
      <ul class="list">
        <li class="flex bdb" v-for="(item,index) in list" :key="index">
          <div class="flex">
            <span class="icon">{{item.name || '' | strFirst}}</span>
            <span>{{item.name}}</span>
          </div>
          <div>{{item.currency_name}}</div>
          <div>{{item.create_time}}</div>
          <div>{{item.legal_balance || '0.000' | numFilters(3)}}</div>
          <div>
            <router-link :to="{path:'/legalShopDetail',query:{id:item.id}}">{{$t('fat.enterShop')}}</router-link>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      page: 1,
      skins: localStorage.getItem("skin") || "days"
    };
  },
  filters: {
    strFirst:function(value){
      return value.substr(0, 1)
    }
  },
  beforeCreate() {
    if (
      localStorage.getItem("skin") &&
      localStorage.getItem("skin") == "nights"
    ) {
      document
        .querySelector("html")
        .setAttribute("style", "background-color:#000;");
    } else {
      document
        .querySelector("html")
        .setAttribute("style", "background-color:#f8f6f6;");
    }
  },
  beforeDestroy() {
    document.querySelector("html").removeAttribute("style");
  },
  created() {
    var token = window.localStorage.getItem("token") || "";
    if (token) {
      this.token = token;
      this.getList();
    }
  },
  methods: {
    getList() {
      let that = this;
      this.$http({
        url: "/api/my_seller",
        headers: { Authorization: this.token }
      }).then(res => {
        if (res.data.type == "ok") {
          var list = res.data.message.data;
          if (list.length) {
            that.list = list;
          }
        }else if(res.data.type == 998){
          layer.msg(res.data.message);
          setTimeout(function(){
            	that.$router.push({name:'authentication'});
          },500)
        }else{
          layer.msg(res.data.message);
        }
      });
    }
  }
};
</script>

<style lang='scss' scoped>
.white {
  width: 1200px;
  margin: 66px auto 50px;
  min-height: 630px;
  background-color: #fff;
  position: relative;
  top: 18px;
  > .title {
    margin-bottom: 20px;
    padding: 0 30px;
    // background: #f8f8f8;
    line-height: 50px;
  }
  > .list-box {
    line-height: 30px;
    // background: #f8f8f8;
    padding: 0 30px;
    .list-header,
    li {
      > div {
        width: 19.9%;
        height: 30px;
      }
      > div:last-child {
        // text-align: tig
        text-align: right;
      }
    }
    li {
      padding: 14px 0;
      > div:first-child {
        > span:first-child {
          width: 30px;
          height: 30px;
          border-radius: 50%;
          text-align: center;
          background: #f0b90b;
          color: #fff;
          margin-right: 5px;
          line-height: 30px;
        }
      }
    }
    li > div:last-child a{
      float: right;
      padding: 0 14px;
      border-radius: 2px;
      background: #f0b90b;
      color: #fff;
      cursor: pointer;
      font-size: 14px;

    }
    
  }
}
.balck{
  background-color: #1e2b34;
  color: #fff;
}
.balck .bdb{
  border-bottom: 1px solid #252323;
}
</style>



